table {
  border-collapse: collapse;
  text-align: left;
}

table > thead {
  border-bottom: 1px solid #dae1e1;
  color: #808080;
  font-size: 12px;
}

table > thead .mf-icon {
  font-size: inherit;
  vertical-align: middle;
}

table > tbody > tr {
  vertical-align: middle;
}

table > tbody > tr:not(:last-child) {
  border-bottom: 1px solid #dae1e1;
}

table > tbody > tr:hover {
  background-color: #eef2f2;
}

table > tbody > tr:hover > .mf-actions > .mf-icon-button {
  opacity: 1;
}

table > tbody > tr > .mf-actions > .mf-icon-button {
  opacity: 0;
}

table > tbody > tr > .mf-actions > .mf-icon-button:focus {
  opacity: 1;
}

table td, table th {
  padding: 0 10px;
}

table td > a, table th > a {
  padding: 0;
}

table td > a:not(.mf-disabled):hover, table th > a:not(.mf-disabled):hover {
  background-color: transparent;
}

table td > a,
table td > .mf-icon, table th > a,
table th > .mf-icon {
  vertical-align: middle;
}

table td > .mf-icon:first-child, table th > .mf-icon:first-child {
  margin-right: 5px;
}

table tr {
  height: 36px;
  vertical-align: middle;
}

table .mf-icon,
table .mf-icon-button {
  font-size: 20px;
}

[dir="rtl"] table {
  text-align: right;
}

[dir="rtl"] table td > .mf-icon:first-child, [dir="rtl"] table th > .mf-icon:first-child {
  margin-left: 5px;
  margin-right: initial;
}

.mf-tile {
  border: 1px solid transparent;
  color: #434c50;
  cursor: pointer;
  font-size: 12px;
  line-height: 15px;
  position: relative;
}

.mf-tile:focus {
  background-color: #fff6ce;
  border-color: #ffd92d;
  color: #434c50;
  outline: none;
}

.mf-tile:hover {
  background-color: #f6f9f8;
  border-color: #28a9e1;
  color: #434c50;
}

.mf-tile.mf-tiny {
  height: 72px;
  max-width: 72px;
  width: 72px;
}

.mf-tile.mf-tiny > .mf-avatar {
  height: 50px;
  width: 50px;
}

.mf-tile.mf-tiny > .mf-icon {
  font-size: 50px;
}

.mf-tile.mf-tiny > .mf-tile-content {
  display: none;
}

.mf-tile.mf-small {
  display: flex;
  flex-flow: row nowrap;
  height: 72px;
  width: 240px;
}

.mf-tile.mf-small > .mf-avatar {
  height: 50px;
  width: 50px;
}

.mf-tile.mf-small > .mf-icon {
  font-size: 50px;
}

.mf-tile.mf-small > .mf-tile-content {
  margin: 10px 10px 10px 0;
}

.mf-tile.mf-small > .mf-tile-content > :not(h3) {
  display: none;
}

.mf-tile.mf-small > .mf-tile-content > h3 {
  font-size: 20px;
  line-height: 50px;
}

.mf-tile.mf-medium {
  display: flex;
  flex-flow: row nowrap;
  height: 72px;
  width: 240px;
}

.mf-tile.mf-medium > .mf-avatar {
  flex: 0 0 50px;
  height: 50px;
  width: 50px;
}

.mf-tile.mf-medium > .mf-icon {
  font-size: 50px;
}

.mf-tile.mf-medium > .mf-tile-content {
  margin: 10px 10px 10px 0;
  height: 50px;
  overflow: hidden;
}

.mf-tile.mf-medium > .mf-tile-content > h3 {
  font-size: 15px;
  line-height: 1em;
  margin-bottom: 5px;
}

.mf-tile.mf-large {
  display: flex;
  flex-flow: column nowrap;
  height: 197px;
  max-width: 160px;
  text-align: center;
  width: 160px;
}

.mf-tile.mf-large > .mf-avatar,
.mf-tile.mf-large > .mf-icon {
  margin: 10px auto;
}

.mf-tile.mf-large > .mf-avatar {
  flex: 0 0 100px;
  height: 100px;
  width: 100px;
}

.mf-tile.mf-large > .mf-icon {
  font-size: 100px;
}

.mf-tile.mf-large > .mf-tile-content {
  margin: 0 10px 10px 10px;
  overflow: hidden;
}

.mf-tile.mf-large > .mf-tile-content > h3 {
  font-size: 15px;
  line-height: 1em;
  margin-bottom: 5px;
}

.mf-tile > .mf-avatar {
  margin: 10px;
}

.mf-tile > .mf-icon {
  margin: 10px;
}

.mf-tile > .mf-actions {
  position: absolute;
  top: 0;
  right: 0;
}

.mf-tile > .mf-tile-content > h3,
.mf-tile > .mf-tile-content > p {
  margin: 0;
  padding: 0;
}

[dir="rtl"] .mf-tile.mf-small > .mf-tile-content {
  margin: 10px 0 10px 10px;
}

[dir="rtl"] .mf-tile.mf-medium > .mf-tile-content {
  margin: 10px 0 10px 10px;
}

[dir="rtl"] .mf-tile > .mf-actions {
  left: 0;
  right: initial;
}

.mf-grid {
  display: flex;
  flex-flow: row wrap;
}

.mf-grid > .mf-tile {
  flex-grow: 0;
  flex-shrink: 0;
  margin-right: 5px;
  margin-bottom: 5px;
  vertical-align: top;
}

.mf-grid.mf-stacked {
  display: block;
}

.mf-grid.mf-stacked > .mf-tile {
  width: auto;
}

.mf-grid.mf-stacked > .mf-tile:not(:last-child) {
  margin-bottom: 5px;
}

[dir="rtl"] .mf-grid > .mf-tile {
  margin-left: 5px;
  margin-right: initial;
}

body {
  background-color: #ffffff;
  color: #434c50;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
}
